import React, { Component } from 'react'

class CollapsePanel extends Component {
  constructor(props) {
    super(props)
    this.state = {
      collapse: false
    }
    this.togglePanel = this.togglePanel.bind(this)
  }
  componentWillMount() {
    this.setState({
      collapse: this.props.defalutCollpase
    })
  }
  togglePanel() {
    const collapse = !this.state.collapse
    this.setState({
      collapse
    })
    this.props.onChange(collapse)
  }
  render() {
    const { maxHeight, style } = this.props
    const { collapse } = this.state
    return (
      <div style={style}>
        <div className="max-height-transition" style={{ maxHeight: collapse ? 0 : maxHeight, overflow: 'hidden' }} >
          {this.props.children}
        </div>
        <div onClick={this.togglePanel} className="text-center bottom-border pointer">
          {
            collapse ?
              <i className="fa fa-angle-down fa-2x" aria-hidden="true"></i> :
              <i className="fa fa-angle-up fa-2x" aria-hidden="true"></i>
          }
        </div>
      </div>
    )
  }
}

CollapsePanel.propTypes = {
  defalutCollpase: React.PropTypes.bool,
  maxHeight: React.PropTypes.string,
  children: React.PropTypes.element,
  onChange: React.PropTypes.func,
  style: React.PropTypes.object
}

CollapsePanel.defaultProps = {
  defalutCollpase: false,
  maxHeight: '1000px',
  onChange: (v) => (v),
  style: {}
}

export default CollapsePanel



